var closeModel = require('./closeModel');
var getOptionModel = require('../modules/model/getOptionModel');

var optionTemplate = require('../templates/option-template.ejs');

function applyChages(callback) {
    closeModel();
    var rightSelectorOptions = document.querySelector(
        "#slush_right").options;
    var leftSelectorOptions = document.querySelector(
        "#slush_left").options;
    var availableOptions = [];
    var selectedOptions = [];

    Array.prototype.forEach.call(
        leftSelectorOptions,
        function (option) {
            availableOptions.push(getOptionModel(
                option.value,
                option.label
            ))
        });

    Array.prototype.forEach.call(
        rightSelectorOptions,
        function (option) {
            selectedOptions.push(getOptionModel(
                option.value,
                option.label
            ))
        });

    callback(availableOptions, selectedOptions)
}

module.exports = applyChages;



