var mainComponent = require('./templates/model-template.ejs');
var createDomElement = require('./utils/createDomElement');
var getModel = require('./modules/model/getModel');
var getOptionModel = require('./modules/model/getOptionModel');
var closeModel = require('./functions/closeModel');
var cancelChanges = require('./functions/cancelChanges');
var applyChanges = require('./functions/applyChanges');
var resetChanges = require('./functions/resetChanges');
var moveDownSelected = require('./functions/moveDownSelected');
var moveUpSelected = require('./functions/moveUpSelected');
var moveLeftSelectedItems = require('./functions/moveLeftSelectedItems');
var moveRightSelectedItems = require('./functions/moveRightSelectedItems');

function init() {

    var availableSettings = [
        getOptionModel("active", "Active"),
        getOptionModel("activity_due", "Activity due"),
        getOptionModel("work_end", "Actual end"),
        getOptionModel("work_start", "Actual start"),
        getOptionModel("additional_assignee_list", "Additional assignee list")
    ];

    var defaultSelectedSettings = [
        getOptionModel("business_stc", "Business resolve time"),
        getOptionModel("business_service", "Business service"),
        getOptionModel("caller_id", "Caller")
    ];

    document.querySelector('.main-wrapper')
        .appendChild(createDomElement(mainComponent(
            getModel(
                closeModel,
                applyChanges.bind(null, print),
                cancelChanges,
                resetChanges.bind(null, availableSettings, defaultSelectedSettings),
                moveDownSelected,
                moveUpSelected,
                moveRightSelectedItems,
                moveLeftSelectedItems,
                true,
                availableSettings,
                defaultSelectedSettings
            )
        )));
    function print(av, sel) {
        console.log(av);
        console.log(sel)
    }

}

document.addEventListener('DOMContentLoaded', init);




